import React, { useContext } from 'react';
import { AuthErrorContext } from '../constants/context';
import Page from './Page';
import { NavBar, WhiteSpace, Result, Icon, WingBlank, Button } from 'antd-mobile';
import { noop } from '../utils';

export default function AuthErrorHelper() {
    const { error, reload = noop } = useContext(AuthErrorContext);
    return (
        <Page>
            <NavBar mode="light">Auth Failed</NavBar>
            <WhiteSpace size="lg" />

            <Result
                img={<Icon type="cross-circle-o" className="spe" style={{ fill: '#F13642' }} />}
                title="Error"
                message={error.message}
            />
            <WhiteSpace size="lg" />
            <WingBlank>
                <Button type="primary" onClick={reload}>Retry</Button>
            </WingBlank>
        </Page>
    );
}