import React, { useContext, useEffect, useState } from 'react';
import Page from './Page';
import { Button, NavBar, WhiteSpace, WingBlank, List, InputItem, Toast, Modal } from 'antd-mobile';
import { ASYNC_OPERATION_STATUS_CODE } from '../constants/index';
import { NonLoginContentContext } from '../constants/context';
import { noop } from '../utils';

export default function EmailAndPasswordLoginHelper() {
    const { reload, auth, error, clearEnvId = noop } = useContext(NonLoginContentContext);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loginStatus, setLoginStatus] = useState({ code: ASYNC_OPERATION_STATUS_CODE.INIT, state: null });
    const handleClick = async () => {
        if (!email) {
            Toast.fail('Please input email!', 1);
            return;
        } else if (!password) {
            Toast.fail('Please input password!', 1);
            return;
        } else {
            try {
                setLoginStatus({ code: ASYNC_OPERATION_STATUS_CODE.PENDING, state: null });
                const loginState = await auth.signInWithEmailAndPassword(email, password);
                setLoginStatus({ code: ASYNC_OPERATION_STATUS_CODE.SUCCESS, state: loginState });
                reload();
            } catch (err) {
                setLoginStatus({ code: ASYNC_OPERATION_STATUS_CODE.FAIL, state: new Error('Login failed.') });
            }
        }
    };
    useEffect(() => {
        if (loginStatus.code === ASYNC_OPERATION_STATUS_CODE.FAIL) {
            Modal.alert('Oops!', `error: ${loginStatus.state.message}, please check and try again.`, [{ text: 'Ok ' }])
        }
    }, [loginStatus.code, loginStatus.state])
    return (
        <Page>
            <NavBar mode="light" rightContent={<Button inline size="small" onClick={() => {
                Modal.alert('Warn', 'Are you sure Reset Resource Id?', [
                    { text: 'Cancel', onPress: () => console.log('cancel') },
                    { text: 'Ok', onPress: () => clearEnvId() },
                ])
            }} >Reset ID</Button>} >Login</NavBar>
            <WhiteSpace size="lg" />
            <List>
                <InputItem
                    clear
                    value={email}
                    onChange={value => setEmail(value)}
                    placeholder="Please input email address"
                >Email</InputItem>
                <InputItem
                    clear
                    type="password"
                    value={password}
                    onChange={value => setPassword(value)}
                    placeholder="Please input password"
                >Password</InputItem>
            </List>
            <WhiteSpace size="lg" />
            <WingBlank>
                <Button loading={loginStatus.code === ASYNC_OPERATION_STATUS_CODE.PENDING} type="primary" onClick={handleClick}>Submit</Button>
            </WingBlank>
        </Page>
    );
}