import "babel-polyfill"

import React, { useContext, useEffect, useState } from 'react';
import cloudbase from '@cloudbase/js-sdk';
import { getCachedEnvId, setCacheEnvId, noop, guid } from './utils/index';
import { NonLoginContentContext, UserContentContext, AuthErrorContext } from './constants/context';
import { ASYNC_OPERATION_STATUS_CODE } from './constants/index';
import SetEnvironmentIdHelper from './components/SetEnvironmentIdHelper';
import EmailAndPasswordLoginHelper from './components/EmailAndPasswordLoginHelper';
import AuthErrorHelper from "./components/AuthErrorHelper";
import 'antd-mobile/dist/antd-mobile.css';

const cachedEnvId = getCachedEnvId();
const initLoginStatusCheckId = guid();

export default function App() {
    const [envId, setEnvId] = useState(cachedEnvId);
    if (!envId) {
        return (
            <SetEnvironmentIdHelper handleSetCacheEnvId={id => {
                setEnvId(id);
                setCacheEnvId(id);
            }} />
        );
    } else {
        return (
            <UserContentWrap envId={envId} clearEnvId={() => {
                setEnvId('');
                setCacheEnvId('');
            }} login={<EmailAndPasswordLoginHelper />} error={<AuthErrorHelper />}>
                <TestUserContent />
            </UserContentWrap>
        );
    }
}

function UserContentWrap(props) {
    const { clearEnvId } = props;
    const cloundBaseApp = cloudbase.init({
        env: props.envId,
    });
    const auth = cloundBaseApp.auth({
        persistence: "local"
    });
    const [loginStatus, setLoginStatus] = useState({ code: ASYNC_OPERATION_STATUS_CODE.INIT, state: null });
    const [fetchVersion, setFetchVersion] = useState(initLoginStatusCheckId);
    useEffect(() => {
        async function main() {
            try {
                setLoginStatus({ code: ASYNC_OPERATION_STATUS_CODE.PENDING, state: null });
                const loginState = await auth.getLoginState();
                setLoginStatus({ code: ASYNC_OPERATION_STATUS_CODE.SUCCESS, state: loginState });
            } catch (err) {
                setLoginStatus({ code: ASYNC_OPERATION_STATUS_CODE.FAIL, state: err });
            }
        };
        main();
    }, [fetchVersion]);
    switch (loginStatus.code) {
        case ASYNC_OPERATION_STATUS_CODE.PENDING: {
            return props.pending || null;
        };
        case ASYNC_OPERATION_STATUS_CODE.SUCCESS: {
            if (!loginStatus.state) return (
                <NonLoginContentContext.Provider value={{ auth, reload: () => setFetchVersion(guid()), clearEnvId }}>
                    { props.login }
                </NonLoginContentContext.Provider>
            );
            return (
                <UserContentContext.Provider value={{ app: cloundBaseApp, user: loginStatus.state.user }} >
                    {props.children}
                </UserContentContext.Provider>
            );
        };
        case ASYNC_OPERATION_STATUS_CODE.FAIL: {
            return (
                <AuthErrorContext.Provider value={{ auth, reload: () => setFetchVersion(guid()), error: loginStatus.state }}>
                    { props.error }
                </AuthErrorContext.Provider>
            );
        };
        case ASYNC_OPERATION_STATUS_CODE.INIT:
        default: {
            return props.placeholder || null;
        }
    }
}

function TestUserContent() {
    const { app, user } = useContext(UserContentContext);
    useEffect(() => {
        const db = app.database();
        const _ = db.command;
        const collection = db.collection('visitRecords');
        collection.where({}).get().then(response => {
            console.log('response', response);
        }).catch(err => {
            console.log('err', err);
        });

        collection.add({ time: Date.now() })
            .then((res) => { console.log('res', res) })
            .catch((e) => { console.log('e', e) });;
    })
    return (
        <>
            <p>success:</p>
            <ol>
                <li>loginType: {user.loginType}</li>
                <li>nickName: {user.nickName | '-'}</li>
                <li>email: {user.email}</li>
                <li>hasPassword: {user.hasPassword ? 'true' : 'false'}</li>
            </ol>
        </>
    );
}
