import React, { useState } from 'react';
import { Button, NavBar, WhiteSpace, WingBlank, List, InputItem, Toast } from 'antd-mobile';
import Page from './Page';
import { noop, getCachedEnvId, removeCacheEnvId } from '../utils/index';

const cachedEnvId = getCachedEnvId();
export default function SetEnvironmentIdHelper(props) {
    const { handleSetCacheEnvId = noop } = props;
    const [val, setVal] = useState(cachedEnvId);
    const handleClick = () => {
        if (typeof val !== 'string') return;
        if (val === '') {
            removeCacheEnvId();
            return;
        }
        if (val.length <= 16) {
            Toast.fail('Invalid Resource ID!', 2);
            return;
        }
        handleSetCacheEnvId(val);
    };
    return (
        <Page>
            <NavBar mode="light">App Config</NavBar>
            <WhiteSpace size="lg" />
            <List>
                <InputItem
                    clear
                    value={val}
                    onChange={value => setVal(value)}
                    placeholder="Please Input Resource Id"
                >ID</InputItem>
            </List>
            <WhiteSpace size="lg" />
            <WingBlank>
                <Button type="primary" onClick={handleClick}>Save</Button>
            </WingBlank>
        </Page>
    );
}